import { Component } from '@angular/core';
import { IonicPage, ModalController, NavController, ViewController, Events, NavParams } from 'ionic-angular';
import { FormGroup,FormBuilder,Validators } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { Session, NativeService } from '../../../services/services';
import { UserProvider } from '../../../providers/providers';

@IonicPage()
@Component({
  selector: 'page-update-signature',
  templateUrl: 'update-signature.html',
})
export class UpdateSignaturePage {

  currentUser: any;
    isReadyToSave: boolean;
    form: FormGroup;

    constructor(public navCtrl: NavController,
        public viewCtrl: ViewController,
        public session: Session,
        private userProvider: UserProvider,
        private nativeService: NativeService,
        private translateService: TranslateService,
        public formBuilder: FormBuilder,
        public events: Events,
        public modalCtrl: ModalController,
        public navParams: NavParams) {
 
        if (this.session.user != null) {
            this.currentUser = this.session.user;
        }
        this.form = formBuilder.group({
            signature: [this.navParams.data.signature || '', Validators.required]
        });

        // Watch the form for changes, and
        this.form.valueChanges.subscribe((v) => {
            this.isReadyToSave = this.form.valid;
        });
    }
/*
    submit() {
        if (!this.form.valid) { return; }
        this.form.controls['username'];
        //校验
        if(this.form.value.signature != null && this.form.value.signature != undefined){
            this.nativeService.alert(this.translateService.instant("MY_NICKNAME_VERIFY"));
            return
        }

        this.userProvider.setNickname({
            nickname : this.form.value.nickname
        }).subscribe((data: any) => {
            this.userProvider.getUserInfo().subscribe((data: any) => {
                this.events.publish('user:info-changed', Date.now());
                this.viewCtrl.dismiss();
            });  
        });
    }
  */


}



